<template>
  <div>
    <div class="nav-bar noshadow">
      <div class="nav-bar-left" @click="goTbGzrz()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-search">
        <div class="header-search">
          <search-box ref="searchBox" @query="onQueryChange" />
          <!-- <van-button type="info" round size="small" @click="submit()">搜索</van-button> -->
        </div>
      </div>
    </div>

    <div class="container">
      <!-- 常用项目 begin -->
      <!-- <div class="content">
        <div class="common-main box">
          <div class="title">
            <div class="title-left">
              <h2>常用项目</h2>
            </div>
          </div>

          <div class="select-rzxm">
            <dl v-for="(item,index) of cyxmList" :key="index" :class="checkXm.id===item.id?'active':''" @click="xzXm(item)">
              <dt>{{ item.xmmc }}</dt>
              <dd @click="delCyxm(item)" @click.stop=""><i class="iconfont iconshanchuicon" /></dd>
            </dl>
          </div>
        </div>
      </div> -->
      <!-- 常用项目 end -->

      <!-- 选择项目 begin -->
      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>选择项目</h2>
            </div>
          </div>
          <!-- 通用标题 end -->

          <div class="select-rzxm">
            <!-- <van-list
              v-model="loading"
              :finished="finished"
              :finished-text="finishedText"
              :error-text="errorText"
              @load="onLoad"
            > -->
            <dl v-for="(item,index) of xmList" :key="index" :class="checkXm.id===item.id?'active':''" @click="xzXm(item)">
              <dt>{{ item.xmmc }}</dt>
              <!-- <dd @click="addCyxm(item,index)" @click.stop=""><i class="iconfont iconzengjiaicon" /></dd> -->
            </dl>
            <!-- </van-list> -->
          </div>
        </div>
      </div>
      <!-- 选择项目 end -->

    </div>
    <!-- <div class="foot-btn fixed">
      <ul>
        <li><van-button type="info" round size="large" @click="goBack()">确定</van-button></li>
      </ul>
    </div> -->
  </div>
</template>

<script land="script" scoped>
import SearchBox from '@/components/search/search-box'
import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'XzGzrzXm',
  components: {
    SearchBox
  },
  mixins: [pageRouter],
  data() {
    return {
      xmList: [],
      loading: false,
      finished: false,
      totalPages: 1,
      currentPage: 1,
      finishedText: '',
      errorText: '',
      cyxmList: [],
      checkXm: ''
    }
  },
  // activated() {
  //   if (window.localStorage.getItem('cyxmList')) {
  //     this.cyxmList = JSON.parse(window.localStorage.getItem('cyxmList'))
  //   }
  // },
  created() {
    this.rq = this.$store.state.erp.rq
    // if (window.localStorage.getItem('cyxmList')) {
    //   this.cyxmList = JSON.parse(window.localStorage.getItem('cyxmList'))
    // }
    this.getXmxxList()
  },
  methods: {
    goBack() {

    },
    xzXm(checkXm) {
      this.$store.dispatch('erp/isGdByDateAndXmAction', {
        date: this.rq,
        xm: checkXm.id
      }).then(res => {
        if (res.state === 1) {
          this.checkXm = checkXm
          this.$store.commit('erp/SET_CHECKXM', this.checkXm)
          this.$router.back()
        } else {
          this.$toast(res.text)
        }
      }).catch(() => {
      })
    },
    delCyxm(cyxm) {
      for (let i = 0; i < this.cyxmList.length; i++) {
        if (cyxm.id === this.cyxmList[i].id) {
          this.cyxmList.splice(i, 1)
          this.xmList.unshift(cyxm)
        }
      }
      window.localStorage.setItem('cyxmList', JSON.stringify(this.cyxmList))
    },
    addCyxm(cyxm, index) {
      let state = false
      for (let i = 0; i < this.cyxmList.length; i++) {
        if (this.cyxmList[i].id === cyxm.id) {
          this.$toast('已添加至常用项目')
          state = true
        }
      }
      if (state) {
        return
      } else {
        this.xmList.splice(index, 1)
        this.cyxmList.push(cyxm)
      }
      window.localStorage.setItem('cyxmList', JSON.stringify(this.cyxmList))
    },
    // onLoad() {
    //   if (this.currentPage <= this.totalPages) {
    //     this.getXmxxList()
    //   } else {
    //     this.finished = true
    //   }
    // },
    getXmxxList() {
      this.$store.dispatch('erp/getXmxxListAction', {
        xmmc: this.xmmc,
        currentPage: this.currentPage,
        pageSize: 10
      }).then(res => {
        this.currentPage++
        this.loading = false
        this.totalPages = res.totalPages
        for (let i = 0; i < res.xmxxArr.length; i++) {
          // let state = false
          // for (let j = 0; j < this.cyxmList.length; j++) {
          //   if (res.xmxxArr[i].id === this.cyxmList[j].id) {
          //     state = true
          //   }
          // }
          // if (!state) {
          this.xmList.push(res.xmxxArr[i])
          // }
        }
        if (this.currentPage <= this.totalPages) {
          this.getXmxxList()
        } else {
          this.finished = true
        }
      }).catch(error => {
        this.$toast(error)
      })
    },
    onQueryChange(xmmc) {
      this.xmmc = xmmc
      this.currentPage = 1
      this.xmList = []
      this.getXmxxList()
    },
    goTbGzrz() {
      this.$router.push('TbGzrz')
    }
  }
}
</script>

<style scoped>
/deep/.search-box .van-search .van-cell .van-field__clear{right:8px;}
</style>
