<template>
  <div class="search-box">
    <!--    <i class="icon-search"></i>-->
    <!--    <input :placeholder="placeholder" class="box" ref="query" v-model="query"/>-->
    <!--    <i @click="clear" class="icon-dismiss" v-show="query"></i>-->
    <div class="search-icon">
      <i class="iconfont iconsousuo" />
    </div>
    <van-search
      ref="query"
      v-model="query"
      :maxlength="50"
      is-amount
      shape="round"
      show-action
      placeholder="请输入搜索条件"
      @search="onSearch"
    >
      <div slot="action" @click="onSearch"><van-button round size="small" type="info">搜索</van-button></div>
    </van-search>

    <!-- <div class="search-right" @click="goSearch">
      <i class="iconfont iconyoujiantou" />
    </div> -->

  </div>
</template>

<script type="text/ecmascript-6">
import { debounce } from '../../common/js/util'
export default {
  props: {
    placeholder: {
      type: String,
      default: '请输入搜索条件'
    },
    searchInput: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      query: ''
    }
  },
  watch: {
    searchInput(val) {
      this.query = val
    }
  },
  created() {
    // 监测query数据变化并 将变化结果返回给外部组建search页面
    // this.$watch('query', debounce((newQuery) => {
    //   this.$emit('query', newQuery)
    // }, 200))
  },
  methods: {
    onSearch() {
      // this.$emit('goSearch', this.query)
      this.$emit('query', this.query)
    },
    clear() {
      this.query = ''
    },
    // 父组件传递过来的搜索条件，进行填充
    setQuery(query) {
      this.query = query
      this.$emit('query', this.query)
    },
    blur() {
      this.$refs.query.blur()
    },
    goSearch() {
      this.$emit('goSearch')
    }
  }
}
</script>

